import React from 'react';
import './ChargeAlert.css';
import { Link } from 'react-router-dom';
import { useDispatch, useSelector } from "react-redux/es/exports";
import { useState } from 'react';

const ChargeAlert=(props)=>{
    const name=useSelector(state=>state.Device.name);
    const operator=useSelector(state=>state.Device.operator);
    const password=useSelector(state=>state.Device.password);
    const number=useSelector(state=>state.Device.number);
    const [passwordCharge,setPasswordCharge]=useState('');
    const ChargeCode=(operator==' ' ? '*140*%23':(operator=='' ? '*141*':(operator=='' ? '*140*':'')));
    

    let chargeLink='sms://'+number+'?&body=*'+password+'%23charge:'+ChargeCode+passwordCharge+'%23';


    const set_charge_code=(event)=>{
        setPasswordCharge(event.target.value);

    }
    return (
      <div className="ChargeAlert">
            <div className="alert-ChargeAlert">
                <div className="part-ChargeAlert" id="part1-ChargeAlert">
                    <div className="title-ChargeAlert">
                        <p>    </p>
                    </div>
                    



                </div>
                <div className="part-ChargeAlert" id="part2-ChargeAlert">

                    <div className="inputPart-ChargeAlert">
                        <input type="number" onChange={set_charge_code}/>
                    </div>

                </div>
                <div className="part-ChatrgeAlert" id="part3-ChargeAlert">
                    <div className="controlers-ChargeAlert">
                        <a href={chargeLink}><div className="send-ChargeAlert" onClick={props.close_alert}>
                            <p style={{fontSize:'small'}}></p>


                        </div></a>
                         <div className="cancle-ChargeAlert" onClick={props.close_alert}>
                           <p style={{fontSize:'small'}}></p>
                            
                        </div>
                    </div>

                </div>
                
        
    </div></div>
    )
}

export default ChargeAlert;