import React from "react";
import './SettingG.css';
import { Link } from 'react-router-dom';
import { useState } from "react";


const SettingG = () => {
    // let them=window.localStorage.getItem('theme');
    const oldPassword=localStorage.getItem('password')
    const [nwePassword,setNewPassword]=useState(oldPassword);
    const green='#0afc96';
    const [error,setError]=useState('');
    let them=window.localStorage.getItem('theme')

    const set_new_password=(event)=>{
        setNewPassword(event.target.value);
    }
    const send_new_password=()=>{
        if(nwePassword!=null){
            window.localStorage.setItem('password',nwePassword);
        }
        window.history.back()
    }

    const clear_cache=()=>{
        
        caches.delete('static0002')
        .then(res=>{
            if(res){
                setError('                     ')

            }else {
                setError('')

            }
        });
        
        
        
 
       
    }
    const go_to_page1=()=>{
        window.history.back()
    }
    const set_them=(event)=>{
 
     
        if( them=='white'){
        window.localStorage.setItem('theme','black');
        them='black'
        }else if(them=='black' || them==null){
            window.localStorage.setItem('theme','white')
            them='white'
        }
        event.target.value=(them==='white' ?'' :"")

        
    }
    return(
        <div className="SettingG">
            <div  className='bgb-Setting' id={them==='white' ? 'light_bg':''}>

                <div className="header-Setting">
                   <div className="backPart-Setting" onClick={go_to_page1}>
                    

                    </div>
                    <div className="titlPart-Setting" >
                        <p style={{fontWeight:'400',fontSize:'medium'}}></p>

                    </div>
                    <div className="refresh">
                        <div className=""></div>
                    
                        <div className=''>
                        
                        

                        </div>
                    </div>
                </div>

                <div className="part-SettingG" id={them==='white' ? 'light_bg':''}>

                <div className="item-SettingG">
                        <input type="text" autoComplete="off" className="password-SettingG" id={them=='white'? 'lightButtonnb_bg':''} defaultValue={oldPassword} onChange={set_new_password}/>
                        <span >  </span>
                    </div>
                    <div className="item-SettingG">
                        <input type='button' onClick={set_them} className='changeThem' id={them==='white' ? 'lightButton_bg':''} value={them==='white' ? '':''} />
                        <span>:  </span>
                    </div>
                    <div className="item-SettingG">
                        <Link to={''}><input type='button' onClick={clear_cache} className={them=='white'?'updateL-SettigG':'update-SettigG'} id={them=='white' ? 'lightButton_bg':''} /></Link>
                        <span>:   </span>
                    </div>


                    <div id="error-SettingG" style={{color:green}}>{error}</div>
                    
                    
                    <div><button onClick={send_new_password} id={them==='white' ? 'lightButton_bg':''}></button></div>
                    
                </div>

            </div>
        </div>
    )
}

export  default SettingG