import React, { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux/es/exports";
import './Information.css';
import { Link } from "react-router-dom";
import { changeDevice ,Devices,changeDeviceT} from "../actions";
import { changeBG } from "../actions";
import { click } from "@testing-library/user-event/dist/click";


const Information=(props)=>{

    let them=window.localStorage.getItem('theme')
    const [error,setError]=useState('');
    const [passwordOk,setPasswordOk]=useState(1);
    const [nameok,setNameOk]=useState(1);
    const [numberOk,setNumberOk]=useState(1);
    const deviceName=useSelector(state=>state.Device.name);
    const deviceNumber=useSelector(state=>state.Device.number);
    const devicePassword=useSelector(state=>state.Device.password);
    const deviceOperator=useSelector(state=>state.Device.operator);
    const deviceModel=useSelector(state=>state.Device.model);
    // const [refresh,setRefresh]=useState(false)
    const allData=props.data;




    const [deviceMo,setDeviceMo]=useState(deviceModel);
    const [deviceOp,setDeviceOp]=useState(deviceOperator);
    const [deviceNa,setDeviceNa]=useState(deviceName);
    const [deviceNu,setDeviceNu]=useState(deviceNumber);
    const [devicePa,setDevicePa]=useState(devicePassword);


    const [saveLink,setSaveLink]=useState('/');



    const [selectorM,setSelectorM]=useState(false);
    const [selectorO,setSelectorO]=useState(false);


    const dispatch=useDispatch();
    

    const go_to_setting=async()=>{
        
        window.history.back()

    }


    const change_select_model=()=>{
        setSelectorM(!selectorM)
        setSelectorO(false);
    }
    const change_select_operator=()=>{
        setSelectorO(!selectorO);
        setSelectorM(false)


    }
    const delete_value=(event)=>{
        
        event.target.value='';
        close_all_selector();
        switch(event.target.id){
            case'name':
            set_error(devicePa,'',deviceNu)
            break;
            case 'number':
                set_error(devicePa,deviceNa,'')
                break;
            case 'password':
                set_error('',deviceNa,deviceNu)
                break;

            default:
                break;

        }
        

    }

    const set_error=async(password,name,number)=>{
        //1=is ok
        //2=is bad data
        //3=is data reaoeat

        setNameOk(1)
        setNumberOk(1)
        setPasswordOk(1)
        if((password.length<4) || (password.length>12)){
            // passwordOk=2;
            setError('     4  12  ')
            setPasswordOk(2)

            // alert('error 1 passw')
            // setSaveButton()

        }else{
            // setError('no-error')

        }

        if(name.length!=0){
            
            for(let i=0 ;i<allData.length;i++){
                if(allData[i].name!=deviceName ){
                    if(allData[i].name==name){
                        // nameOk=3;
                        setError('      ');
                        setNameOk(2)
                       

                        break;
                    }
                }
                // setError('no-error')
            }
        }else{
            setError('     ');
           
            setNameOk(2);
           
            
        }
        // alert('number test'+number.slice(0,1)=='0')
        if(!(number.length==11) || !(number.slice(0,1)=='0') || !(number.slice(1,2)=='9')){
            // numberOk=2;
            setError('    ')
            setNumberOk(2);


        }else{
            for(let i=0 ;i<allData.length;i++){
                if(allData[i].name!=deviceName ){
                    if(allData[i].number==number){
                        // nameOk=3;
                        setError('        ');
                        setNumberOk(3)
                       

                        break;
                    }
                }
                
            }

        }

       
    



     }


    const set_name_device=(event)=>{
        setError('')
        
        
        setDeviceNa(event.target.value)
        set_error(devicePa,event.target.value,deviceNu);
        
    }
    const set_number_device=(event)=>{
        setError('')
        
        setDeviceNu(event.target.value)
        set_error(devicePa,deviceNa,event.target.value);
        
    }
    const set_password_device=async(event)=>{
        setError('')
        
        
        setDevicePa(event.target.value)
        if(devicePassword!=event.target.value){
            if(deviceNumber!=deviceNu){
                setSaveLink('sms://'+deviceNu+'?&bode=*'+devicePassword+'%23')
            }else{

            }
            

        }
        set_error(event.target.value,deviceNa,deviceNu);
        
       
    }
    const set_operator_device=(event)=>{
        setDeviceOp(event.target.innerHTML)
        setSelectorO(false)
       
    }
    const set_model_device=(event)=>{
        setDeviceMo(event.target.innerHTML);
        setSelectorM(false)
        
     }
     const close_all_selector=()=> {
        setSelectorO(false)
        setSelectorM(false)

     }


    const save_new_info=async()=>{
        if(nameok==1 && numberOk==1 && passwordOk==1){

        if(deviceMo==deviceModel){
        dispatch(changeDevice(deviceName,deviceNa,deviceNu,devicePa,deviceMo,deviceOp));
        }else{


          let zonesO=[];
          let remotesO=[];
          let contactO=[];
          let smsO=[];
          let stateO=[];
          let outO=[]  
            for(let i=0 ;i<allData.length;i++){
                if(allData[i].name==deviceName){
                    
                    zonesO=allData[i].zones;
                    remotesO=allData[i].remotes;
                    contactO=allData[i].contact;
                    smsO=allData[i].smss;
                    outO=allData[i].out;
                    stateO=allData[i].states;
                    
                    break;
                    


                }
            }
            
            let remotes=[];
            let smss=[];
            let contacts=[];
            let zones=[];
            let remote;
            let sms;
            let contact;
            let zone;


            switch(deviceMo){
                case 'ECO20':
                    zone=5;
                    remote=10;
                    contact=10;
                    sms=2;

                break;
                case 'AS20':
                    zone=5;
                    remote=10;
                    contact=10;
                    sms=4;
                break;
                case 'AIO21':
                    zone=8;
                    remote=15;
                    contact=10;
                    sms=8;
                break;

            }

            for(let i=0 ;i<zone;i++){
                if(zonesO[i]!=undefined){
                    zones[i]=zonesO[i]

                }else{
                    zones[i]={'name':'','number':(i+1),'state':' '}

                }
                

                
            }
            for(let i=0;i<remote;i++){
                if(remotesO[i]!=undefined){
                    remotes[i]=remotesO[i]

                }else{
                    remotes[i]={'name':''+(i+1),'type':' null','number':(i+1),'state':' '}

                }
                

            }
            for(let i=0;i<contact;i++){
                if(remotesO[i]!=undefined){
                    contacts[i]=contactO[i]

                }else{
                    contacts[i]={number:'',description:' 1',index:1}

                }

            }
            for(let i=0;i<sms;i++){
                if(smsO[i]!=undefined){
                    smss[i]=smsO[i]

                }else{
                    smss[i]={'name':'  '+(i+1),'text':'','number':(i+1)}

                }

            }
            
            dispatch(changeDeviceT({'name':deviceNa,'password':devicePa,'model':deviceMo,'access':'','number':deviceNu,'operator':deviceOp,
            'zones':zones,
            'remotes':remotes,
            'smss':smss,
            'contact':contacts
            ,'states':{'battery':(deviceMo=='AIO21'? stateO.battery:'not'),'beeper':stateO.beeper,'connection':stateO.connection,'work':stateO.work,'phone':(deviceMo!='ECO20' ? stateO.phone:'not'),'ajir':stateO.ajir}
            ,out:outO
        },deviceNa,deviceName))

        } 
    }else{
       
    }

        
    }

    const show_image=(event)=>{
        dispatch(changeBG(event.target.value))
    }
    return(
        <div className="Information">
            <div  className='bg-Page1'>
                <div  className='bgb-Page1' id={them=='white' ? 'light_bg':''}>
                    <div className="header-Setting">
                    <div className="backPart-Setting" onClick={go_to_setting}>
                    

                        </div>
                        <div className="titlPart-Setting" >
                            <p style={{fontWeight:'400',fontSize:'medium'}}>{deviceName}</p>

                        </div>
                        <div className="refresh">
                            <div className=""></div>
                    
                                <div className=''>
                        
                                 {/* <p> </p> */}

                                </div>
                        </div>

                    </div>



                    <div className="part1-Information" id={them=='white' ? 'light_bg':''}>
                        <div className="item-Information">
                            <span className="input-Information"><input type="text" className='inputName-Information' id={them=='white' ? 'lightButtonnb_bg':''} autocomplete="off"   lang="en" defaultValue={deviceName} onChange={set_name_device} onClick={delete_value} /></span>
                            <span className="text-Information"> </span>
                        </div>

                        <div className="item-Information">
                            <span className="input-Information"><input type="number" autocomplete="off"  className="number" id={them=='white' ? 'lightButtonnb_bg':''} lang="en" defaultValue={deviceNumber}  onChange={set_number_device} onClick={delete_value} /></span>
                            <span className="text-Information">  </span>
                        </div>

                        <div className="item-Information">
                            <span className="input-Information"><input type="number" autocomplete="off"  className="password"  id={them=='white' ? 'lightButtonnb_bg':''} lang="en" defaultValue={devicePassword}  onChange={set_password_device} onClick={delete_value} /></span>
                            <span className="text-Information"> </span>
                        </div>

                        <div className="item-Information">
                        <div className="part6Inp-AddDevice part6Inp-Information"  style={{zIndex:'3',color:(them=='white' ? 'black':'')}}>
                            
                        <span className='selectModel-AddDevice'   id={them=='white' ? 'lightButtonnb_bg':''}>
                            <div onClick={change_select_model} className='partIconAdd_CA'><span className={them=='white' ? 'moreIconL-AddDevice':'moreIcon-AddDevice'}></span><span className='selected-AddDevice'>{deviceMo}</span></div>
                                <ul style={{height:selectorM ? '':'0px'}}>
                                    <li onClick={set_model_device}>ECO20</li>
                                    <li onClick={set_model_device}>AS20</li>
                                    <li onClick={set_model_device}>AIO21</li>

                            </ul>
                        </span>
                        <span className='titlInp-AddDevice' > </span>
                    </div>

                    
                        </div>

                        <div className="item-Information">
                        <div className="part6Inp-AddDevice part6Inp-Information" style={{color:(them=='white' ? 'black':'')}}>
                            
                        <span className='selectModel-AddDevice'   id={them=='white' ? 'lightButtonnb_bg':''}>
                            <div onClick={change_select_operator} className='partIconAdd_CA'><span className={them=='white' ? 'moreIconL-AddDevice':'moreIcon-AddDevice'}></span><span className='selected-AddDevice'>{deviceOp}</span></div>
                                <ul style={{height:selectorO ? '':'0px'}}>
                                <li onClick={set_operator_device}> </li>
                                    <li onClick={set_operator_device}></li>
                                    <li onClick={set_operator_device}></li>
                                    <li onClick={set_operator_device}></li>

                            </ul>
                        </span>
                        <span className='titlInp-AddDevice' ></span>
                    </div>

                    
                        </div>
                        <div className="error-Information">
                            <span>{error!='no-error' ? error:''}</span>
                        </div>

                        <Link to={error=='' ? '/':''} onClick={save_new_info}><div className="part3-Information">
                            <button className="save-Infofrmation" id={them=='white' ? 'lightButton_bg':''}></button><br />
             
                        </div></Link>
                        {/* <input type="file" onChange={show_image}/> */}
                    </div>
                 </div>
            </div>
            
            
        // </div>
    )

}

export default Information;