import React from "react";
import { useDispatch, useSelector } from "react-redux";
import './Alert.css';
import { Link } from "react-router-dom";
import { deleteDevice } from "../actions";

const Alert=(props)=>{
    //params
    let title=useSelector(state=>state.Alert.title);
    let description=useSelector(state=>state.Alert.description);
    let operator=useSelector(state=>state.Alert.operator);
    let name=useSelector(state=>state.Alert.name);
    let backTo=useSelector(state=>state.Alert.backTo);
    
    const dispatch=useDispatch()
    const response_yes=()=>{
        switch(operator){
            case 'delete_device':
                dispatch(deleteDevice(name))
                break;
            default:
                break;

        }

        window.history.back()

    }
    const response_no=()=>{
        window.history.back()

    }
    return(
    <div className="Alert">
        <div className="bg-Alert">
            <div className="content-Alert">
                <div className="title-Alert">
                    <p>{title}</p>

                </div>
                <div className="description-Alert">
                    <p>{description}</p>

                </div>
                <div className="controller-Alert">
                    <button onClick={response_yes}></button>
                    <button onClick={response_no}></button>

                </div>

            </div>


        </div>
    </div>
    )
}

export default Alert;