import React from 'react';
import './Setting.css';
import {useSelector,useDispatch} from 'react-redux';
import {HideCommand, VisibleCommand} from '../actions/index';
import { Link } from 'react-router-dom';
import Command from './Command';


const Setting=(props)=>{
    let them=window.localStorage.getItem('theme');
   

    
    const name=useSelector(state=>state.Device.name)
    const dispatch=useDispatch();
    dispatch(VisibleCommand());
    const go_to_home=()=>{
        window.history.back()
        
    }
    const go_to_contact=()=>{
        props.changePage('contact')
    }

    const go_to_sms=()=>{
        props.changePage('sms');
    }

    const go_to_zoon_info=()=>{
        props.changePage('zoonInfo');
        
    }

    const go_to_remotes=()=>{
        props.changePage('remotes');
        

    }
    const go_to_out_put=()=>{
        props.changePage('outPuts');
        
    }

    const go_to_charge=()=>{
        props.changePage('charge');
        

    }
    const go_to_information=()=>{
        props.changePage('information');
        

    }
    return(
        <div className="Setting">
            <Command name={name} isIcon={true}></Command>
            <div  className='bg-Setting'><div  className='bgb-Setting' id={them=='white' ? 'light_bg':''} ><div className="header-Setting">
            <div className="backPart-Setting" onClick={go_to_home}>
                    

                </div>
                <div className="titlPart-Setting" >
                    <p style={{fontWeight:'400',fontSize:'medium'}}>{name}</p>

                </div>
                <div className="refresh">
                    <div className=""></div>
                    
                    <div className=''>
                        
                        

                    </div>
                </div>

            </div>
            <div className="menu-Setting">
                
                    <Link to={"/charge"}><div className='item-Setting' id={them=='white' ? 'lightButtonnb_bg':'item1-Setting'}   onClick={go_to_charge}>
                        <div className="icon-Setting" id={them=='white' ? 'iconChatgeL-Setting':'iconChatge-Setting'}>
                            
                            
                        </div>
                        <div className="text-Setting">
                            <span>  </span>

                        </div>

                    </div></Link>
                    <Link to={"/sms"}><div className='item-Setting' id={them=='white' ? 'lightButtonnb_bg':'item2-Setting'} onClick={go_to_sms}>
                        <div className={"icon-Setting"}  id={them=='white' ? 'messageL-Setting':'message-Setting'}>
                            
                        </div>
                        <div className="text-Setting">
                            <span>  </span>

                        </div>

                    </div></Link>
                    <Link to={"/contact"}><div className='item-Setting' id={them=='white' ? 'lightButtonnb_bg':'item3-Setting'}   onClick={go_to_contact}>
                        <div className="icon-Setting" id={them=='white' ? 'contactL-Setting':'contact-Setting'}>
                            
                        </div>
                        <div className="text-Setting">
                            <span>  </span>

                        </div>

                    </div></Link>
                
                
                    <Link to={"/outPut"}><div className='item-Setting' id={them=='white' ? 'lightButtonnb_bg':'item4-Setting'}   onClick={go_to_out_put}>
                        <div className="icon-Setting"  id= {them=='white' ? 'outPutL-Setting':'outPut-Setting'}>

                        </div>
                        <div className="text-Setting">
                            <span> </span>

                        </div>

                    </div></Link>
                    <Link to={'/remote'}><div className='item-Setting' id={them=='white' ? 'lightButtonnb_bg':'item5-Setting'}   onClick={go_to_remotes}>
                        <div className="icon-Setting" id={them=='white' ? 'remoteL-Setting':'remote-Setting'}>

                        </div>
                        <div className="text-Setting" >
                            <span> </span>

                        </div>

                    </div></Link>
                    <Link to={'/zon'}><div className='item-Setting' id={them=='white' ? 'lightButtonnb_bg':'item6-Setting'}   onClick={go_to_zoon_info}>
                        <div className="icon-Setting" id={them=='white' ? 'zoneL-Setting':'zone-Setting'}>

                        </div>
                        <div className="text-Setting" >
                            <span> </span>

                        </div>

                    </div></Link>


                
                
                    <Link to={'/info'}><div className='item-Setting' id={them=='white' ? 'lightButtonnb_bg':'item7-Setting'}   onClick={go_to_information}>
                        <div className="icon-Setting" id={them=='white' ? 'informationL-Setting':'information-Setting'}>

                        </div>
                        <div className="text-Setting" >
                            <span> </span>

                        </div>

                    </div></Link>
                    {/* <div className='item-Setting' id='item2-Setting'>
                        <div className="icon-Setting">

                        </div>
                        <div className="text-Setting">
                            <p>  </p>

                        </div>

                    </div> */}

                
                


            </div></div></div>

        </div>
    )

};

export default Setting;
