import React, { useState } from 'react';
import './AddDevice.css';
import {Devices} from '../actions'
import { Dispatch } from 'react';
import { useDispatch } from 'react-redux';
import { Link } from 'react-router-dom';
const AddDevice=(props)=>{
    let them=window.localStorage.getItem('theme')
    const[name,setName]=useState('');
    const[number,setNumber]=useState('');
    const[password,setPassword]=useState('');
    const[selectModel,setSelectModel]=useState(false);
    const[selectAccess,setSelectAccess]=useState(false);
    const[selectPort,setSelectPort]=useState(false);
    const[selectOperator,setSelectOperator]=useState(false);
    const [model,setModel]=useState('ECO20');
    const [access,setAccess]=useState('');
    const [port,setPort]=useState(' ');
    const [operator,setOperator]=useState(' ');
    const dispatch=useDispatch();
    const [error,setError]=useState('');
    const allData=window.localStorage.getItem('devices')!=null ? JSON.parse(window.localStorage.getItem('devices')):[]
    
    const change_select_model=()=>{
        setSelectModel(!selectModel);
        setSelectOperator(false);

    }
    const change_select_access=()=>{
        setSelectAccess(!selectAccess);
    }
    const change_select_port=()=>{
        setSelectPort(!selectPort);
    }
    const change_select_operator=()=>{
        setSelectOperator(!selectOperator);
        setSelectModel(false);
    }
    const set_model_device=(event)=>{
        setError('');
        setModel(event.target.outerText)
        setSelectModel(!selectModel)
    }
    const set_access_device=(event)=>{
        setAccess(event.target.outerText);
        setSelectAccess(!selectAccess);
    }
    const set_port_device=(event)=>{
        setPort(event.target.outerText);
        setSelectPort(!selectPort);
    }
    const set_operator_device=(event)=>{
        setError('');
        setOperator(event.target.outerText);
        setSelectOperator(!selectOperator);

    }
    const set_number_device=(event)=>{

        setError('');
        // alert(event.target.value.slice(1,2))
        if((event.target.value.slice(0,1)==0 || event.target.value.slice(0,1)=='')  && (event.target.value.slice(1,2)==9 || event.target.value.slice(1,2)=='') && (event.target.value.length<=11)){
        setNumber(event.target.value);
        }else if(!(event.target.value.slice(0,1)==0 || event.target.value.slice(0,1)=='')  || !(event.target.value.slice(1,2)==9 || event.target.value.slice(1,2)=='')){
            event.target.value='';
            setError('     ');
        }else if(!(event.target.value.length<=11)){
            event.target.value=number;
            setError('       11  ');

        }

    }
    const close_selectore=()=>{
        setSelectModel(false);
        setSelectOperator(false);

    }
    const set_name_device=(event)=>{
  
        setError('');
        setName(event.target.value)
        
    }
    const set_password_device=(event)=>{

        if(event.target.value.length<=12){
        setError('');
        setPassword(event.target.value);
        // event.target.value=password;

        }else{
            event.target.value=password;
            setError('      12  ')
        }
        
    }
    const add_device=async()=>{
        let nameOk=(0<name.length   ? 0:1) ;
        let passwordOk=((4<=password.length && password.length<=12)? true:false) ;
        let numberOk=((9000000000<=number && number<=9999999999) ? 0:1);
        for(let item of allData){
            if(item.name==name){
                nameOk=2

            }

            if(item.number==number){
                numberOk=2


            }
            

        }
        
        
        if(passwordOk && nameOk==0 && numberOk==0){
        dispatch(Devices({'name':name,'password':password,'model':model,'access':access,'number':number,'operator':operator,
        'zones':(model==('ECO20' ) ? [{'name':'','number':'1','state':' '},{'name':'','number':'2','state':' '},{'name':'','number':'3','state':' '},{'name':'','number':'4','state':' '},{'name':'','number':'5','state':' '}]:(model=='AS20'?[{'name':'','number':'1','state':' '},{'name':'','number':'2','state':' '},{'name':'','number':'3','state':' '},{'name':'','number':'4','state':' '},{'name':'','number':'5','state':' '}]:[{'name':'','number':'1','state':' '},{'name':'','number':'2','state':' '},{'name':'','number':'3','state':' '},{'name':'','number':'4','state':' '},{'name':'','number':'5','state':' '},{'name':'','number':'6','state':' '},{'name':'','number':'7','state':' '},{'name':'','number':'8','state':' '}]) ),
        'remotes':(model==('ECO20' || 'AS20') ? [{'name':'1 ','type':' ','number':' 1','state':' '},{'name':'2 ','type':' ','number':'2','state':' '},{'name':'3','type':' ','number':' 3','state':' '},{'name':'4','type':' ','number':'4','state':' '},{'name':'5 ','type':' ','number':'5','state':' '},{'name':'6 ','type':' ','number':'6','state':' '},{'name':'7 ','type':' ','number':'7','state':' '},{'name':'8','type':' ','number':'8','state':' '},{'name':'9','type':' ','number':'9','state':' '},{'name':'10','type':' ','number':'10','state':' '}]:(
                    model=='AIO21' ? [{'name':' ','type':' ','number':' 1','state':' '},{'name':' 2','type':' ','number':'2','state':' '},{'name':'3 ','type':' ','number':' 3','state':' '},{'name':'4 ','type':' ','number':'4','state':' '},{'name':' 5','type':' ','number':'5','state':' '},{'name':' 6','type':' ','number':'6','state':' '},{'name':' 7','type':' ','number':'7','state':' '},{'name':' 8','type':' ','number':'8','state':' '},{'name':' 9','type':' ','number':'9','state':' '},{'name':' 10','type':' ','number':'10','state':' '},{'name':' 11','type':' ','number':'11','state':' '},{'name':' 12','type':' ','number':'12','state':' '},{'name':' 13','type':' ','number':'13','state':' '},{'name':' 14','type':' ','number':'14','state':' '},{'name':' 15','type':' ','number':'15','state':' '}]:
                    [{'name':'1','type':' ','number':' 1','state':' '},{'name':'2','type':' ','number':'2','state':' '},{'name':'3','type':' ','number':' 3','state':' '},{'name':'4','type':' ','number':'4','state':' '},{'name':'5','type':' ','number':'5','state':' '},{'name':' 6','type':' ','number':'6','state':' '},{'name':' 7','type':' ','number':'7','state':' '},{'name':' 8','type':' ','number':'8','state':' '},{'name':' 9','type':' ','number':'9','state':' '},{'name':' 10','type':' ','number':'10','state':' '}])),
        'smss':(model==('AIO21' || 'AIO21 PT') ? [{'name':'   1','text':'','number':'1'},{'name':'  2','text':'','number':'2'},{'name':'  3','text':'','number':'3'},{'name':'  4','text':'','number':'4'},{'name':'  5','text':'','number':'1'},{'name':'  6','text':'','number':'6'},{'name':'  7','text':'','number':'7'},{'name':'  8','text':'','number':'8'}]:(
                model=='ECO20' ? [{'name':'  1','text':'','number':'1'},{'name':'  2','text':'','number':'2'}]:[{'name':'  1','text':'','number':'1'},{'name':'  2','text':'','number':'2'},{'name':'  3','text':'','number':'3'},{'name':'  4','text':'','number':'4'}]
        )),
        'contact':(model!='AIO21 PT' ? [{number:'',description:' 1',index:1},{number:'',description:' 2',index:2},{number:'',description:' 3',index:3},{number:'',description:' 4',index:4},{number:'',description:' 5',index:5},{number:'',description:' 6',index:6},{number:'',description:' 7',index:7},{number:'',description:' 8',index:8},{number:'',description:' 9',index:9},{number:'',description:' 10',index:10}]:[{number:'',description:' 1',index:1},{number:'',description:' 2',index:2},{number:'',description:' 3',index:3},{number:'',description:' 4',index:4},{number:'',description:' 5',index:5}])
        ,'states':{'battery':(model=='AIO21'?'0':'not'),'beeper':'0','connection':'0','work':'0','phone':(model!='ECO20' ?'0':'not'),'ajir':'0'}
        ,out:[{state:'off',name:' ',key:'o1'},{state:'off',name:' ',key:'o2'}]
    }))
    props.close_add_device();
}else{
    if(!passwordOk){
    setError('   4  12  ')
    }
    switch(nameOk){
        case 1:
            setError('     ');
        break;
        case 2:
            setError('     ');
        break;
        default:
        break;
    }
    switch(numberOk){
        case 1:
            setError('     '  );
        break;
        case 2:
            setError('        ');
        break;
        default:
        break;
    }

}
        

    }

    return(
        <div  className='bg-Page1'><div  className='bgb-Page1' id={them=='white' ? 'light_bg':''}><div className="AddDevice">
            <div className="centerPart-AddDevice" id={them=='white' ? 'light_bg':''}>
                <div className="titl-AddDevice">
                    <p style={{fontSize:'medium',fontWeight:'400'}}>  </p>
                </div>
                <div className="inputs-AddDevice" >

                    <div className="part1Inp-AddDevice" style={{fontSize:'small',fontWeight:'400'}}>
                        <span className='titlInp-AddDevice' > </span>
                        <span className='selectModel-AddDevice' id={them=='white' ? 'lightButton_bg':''}>
                            <div onClick={change_select_model} ><span className={them=='white' ? 'moreIconL-AddDevice':'moreIcon-AddDevice'} ></span><span className='selected-AddDevice'>{model}</span></div>
                                <ul style={{height:selectModel ? '':'0px'}}>
                                    <li onClick={set_model_device}>ECO20</li>
                                    <li onClick={set_model_device}>AS20</li>
                                    <li onClick={set_model_device}>AIO21</li>
                                    {/* <li onClick={set_model_device}>AIO21 PT</li> */}
                            </ul>
                        </span>
                    </div>
                    {model=='AIO21 PT' ? (
                    <div className="part5Inp-AddDevice">
                        <span className='selectModel-AddDevice'>
                            <div onClick={change_select_access}><span className='moreIcon-AddDevice'></span><span className='selected-AddDevice'>{access}</span></div>
                                <ul style={{height:selectAccess ? '':'0px'}}>
                                    <li onClick={set_access_device}></li>
                                    <li onClick={set_access_device}></li>

                            </ul>
                        </span>
                        <span className='titlInp-AddDevice' ></span>
                    </div>) :''}
                    {model=='AIO21 PT' ? (
                    <div className="part6Inp-AddDevice">
                        <span className='selectModel-AddDevice'>
                            <div onClick={change_select_port}><span className='moreIcon-AddDevice'></span><span className='selected-AddDevice'>{port}</span></div>
                                <ul style={{height:selectPort ? '':'0px'}}>
                                    <li onClick={set_port_device}>1</li>
                                    <li onClick={set_port_device}>2</li>
                                    <li onClick={set_port_device}>3</li>
                                    <li onClick={set_port_device}>4</li>
                                    <li onClick={set_port_device}> 5</li>
                            </ul>
                        </span>
                        <span className='titlInp-AddDevice' ></span>
                    </div>) :''}

                    <div className="part2Inp-AddDevice">
                    <span><input type="text" id={them=='white' ?'lightButtonnb_bg':''} autocomplete="off" onChange={set_name_device} onClick={close_selectore}/></span>
                        <span  className='titlInp-AddDevice'> </span>
                        
                    </div>
                    <div className="part3Inp-AddDevice">
                        <span><input type="number" id={them=='white' ?'lightButtonnb_bg':''} lang="en" className='left' autocomplete="off"   maxLength={11} onChange={set_number_device} onClick={close_selectore}/></span>
                        
                        <span  className='titlInp-AddDevice'>  </span>
                    </div>
                    <div className="part4Inp-AddDevice">
                        <span><input type="number" id={them=='white' ?'lightButtonnb_bg':''}  className='left' lang="en" autocomplete="off"   minLength={4} maxLength={12}  onChange={set_password_device} onClick={close_selectore}/></span>
                        <span  className=''> </span>
                    </div>
                    <div className="part7Inp-AddDevice">
                        <span className='selectModel-AddDevice' id={them=='white' ? 'lightButton_bg':''}>
                            <div onClick={change_select_operator}><span className={them=='white' ? 'moreIconL-AddDevice':'moreIcon-AddDevice'}></span><span className='selected-AddDevice'>{operator}</span></div>
                                <ul style={{height:selectOperator ? '':'0px'}}>
                                    <li onClick={set_operator_device}> </li>
                                    <li onClick={set_operator_device}></li>
                                    <li onClick={set_operator_device}></li>
                                    <li onClick={set_operator_device}></li>

                            </ul>
                        </span>
                        <span className='titlInp-AddDevice' > </span>
                    </div>
                    


                </div>
                <div className="error-AddDevice">
                    <span>{error}</span>
                </div>
                <div className="controller-AddDevice" style={{fontSize:'small',fontWeight:'500'}}>
                    <button onClick={add_device} id={them=='white' ? 'lightButton_bg':''}></button>
                    <button onClick={props.close_add_device} id={them=='white' ? 'lightButton_bg':''}></button>

                </div>

            </div>

        </div></div></div>
    )
}

export default AddDevice;