import React, { useState } from 'react';
import './Contact.css';
import ContactAlert from './ContactAlert';
import {useSelector,useDispatch} from 'react-redux';
import {HideCommand, VisibleCommand} from '../actions/index';
import { Link } from 'react-router-dom';

const Contact=(props)=>{
    const dispatch=useDispatch();
    let them=window.localStorage.getItem('theme')
    dispatch(VisibleCommand());
    const number=useSelector(state=>state.Device.number);
    const password=useSelector(state=>state.Device.password);
    const [ALert,setAlert]=useState(false);

    const close_alert=()=>{
        setAlert(false);

    }

    const open_alert=()=>{
        setAlert(true);
        
      

    }


    

    let deleteContact='sms:'+number+'?&body=*'+password+'%23num:'+props.data.index+':'+'del';
    return(
        <div className="Contact" id={them=='white' ? 'light_bg':''}>
            {ALert ? <ContactAlert close_alert={close_alert} index={props.data.index} ></ContactAlert> :''}
            <div className="part-Contact" id='part1-Contact'>
                <div className="addIcon-Contact">
                    <a href={deleteContact}><button id={them=='white' ? 'deletL-Contact':'delet-Contact'}></button></a>

                </div>
                <div className="addIcon-Contact">
                 <button onClick={open_alert} id={them=='white' ? 'editL-Contact':'edit-Contact'} > </button>

                </div>

            </div>
            <div className="part-Contact" id='part2-Contact'>
                <div className="icon-Contact">
                    <span>{props.data.index}</span>

                </div>
                <div className="text-Contact">
                <div className="title-Contact">
                    <p style={{fontSize:'small' ,fontWeight:'400'}}>{props.data.number}</p>

                </div>
                <div className="description-Contact">
                    <p style={{fontSize:'samll',fontWeight:'100'}}>{(props.data.description==='C' ? ' ':(props.data.description==='S' ? ' ':(props.data.description==='CS' ?'  ' :(props.data.description==='E' ? '':(props.data.description==='L'?'':'')))))}</p>

                </div>
                </div>

            </div>

        </div>
    )
}

export default Contact;