import React, { useState } from "react";
import './Remotes.css';
import RemoteAlert from "./RemoteAlert";
import {useSelector,useDispatch} from 'react-redux';
import {HideCommand, VisibleCommand} from '../actions/index';
import { Link } from 'react-router-dom';
import Command from "./Command";

const Remotes=(props)=>{
    let them=window.localStorage.getItem('theme')
    const name=useSelector(state=>state.Device.name);
    const number=useSelector(state=>state.Device.number);
    const password=useSelector(state=>state.Device.password);
    let [index,setIndex]=useState('');
    // let deleteRemote='sms:'+number+'?&body=%23num:'+props.data.number+':del';
    const dispatch=useDispatch();
    dispatch(VisibleCommand());
    const go_to_setting=()=>{
        window.history.back()
    }
    const [remoteAlert,setRemoteAlert]=useState(false);
     async function  change_alert_remote (event){


        setRemoteAlert(!remoteAlert);
        setIndex(event.target.id);
        
        

    }
    return(
        
        <div className="Remotes">
            <Command name={name} isIcon={true}></Command>
                    <div  className='bg-Setting'><div  className='bgb-Setting' id={them=='white' ? 'light_bg':''}>
            {remoteAlert ? <RemoteAlert change_alert={change_alert_remote} index={index}></RemoteAlert> :''}
            <div className="blure"></div>
            <div className="header-Setting">
           <div className="backPart-Setting" onClick={go_to_setting}>
                    

                </div>
                <div className="titlPart-Setting" >
                    <p style={{fontWeight:'400',fontSize:'medium'}}>{name}</p>

                </div>
                 <a href={'sms:'+number+'?&body=*'+password+'%23RMT'+'?'}><div className="refresh">
                    <div className="icon-refresh"></div>
                    
                   <div className="text-refresh" >
                        {/* <p> </p> */}

                    </div>
                </div></a>

            </div>



            
            <div className="part-Remotes" id='part1-Remotes'>

            </div>
            <div className="part-Remotes" id='part2-Remotes'>
                <div className="table-Remotes">
                    <div className="title-Remotes" id={them=='white' ? 'light_bg':''}>
                    <div className="state-Remotes">
                            <p></p>

                        </div>
                    <div className="type-Remotes">
                            <p> </p>

                        </div>
                        <div className="name-Remotes">
                            <p></p>

                        </div>
                        <div className="number-Remotes">
                            <p> </p>

                        </div>
                    </div>
                    <div className="items-Remotes">
                        {
                            props.data.map(item=>{
                                return(
                                    <div className="item-Remotes" id={them=='white' ? 'light_bg':''}>
                                        <div className="states-remotes" >
                                        <div className={them=="white" ? 'editL-Remotes':"edit-Remotes"}  id={item.number} onClick={change_alert_remote}>
                                            
                                            <p id={item.number}></p>
                                            

                                        </div>
                                        <a href={'sms:'+number+'?&body=*'+password+'%23RMT:DL:'+item.number} id={them=='white' ?'border_light':''}>
                                            <div className={them=="white" ? "stateL-Remotes":"state-Remotes"} id={item.number}>
                                            
                                            <p></p>
                                            

                                            </div>
                                        </a>
                                        <a href={'sms:'+number+'?&body=*'+password+'%23RMT:DS:'+item.number} id={them=='white' ?'border_light':''}><div className={them=="white" ? "stateL-Remotes":"state-Remotes"}  id={item.number}>
                                            
                                            <p id={item.number}></p>
                                            

                                        </div></a>
                                        </div>
                                        <div className="type-Remotes">
                                            <p >{(item.state=='1' ? ' ':(item.state=='2' ? '/':(item.state=='3' ? ' ':(item.state=='4' ? ' ':''))))}</p>
                                            
                                        </div>
                                        <div className="name-Remotes">
                                            <p >{item.name}</p>
                                            
                                        </div>

                                         <div className="number-Remotes">
                                            <p >{item.number}</p>
                                            
                                        </div>
                                            

                                    </div>
                                )
                            })
                            
                        }

                    </div>
                    {/* <div className="add-Remotes">
                        <div className="addIcon-Remotes">

                        </div>

                    </div> */}

                </div>
                
            </div>
            </div></div></div>

        
    )
}

export default Remotes;