import React, { useState } from "react";
import './Charge.css';
import { useDispatch, useSelector } from "react-redux/es/exports";
import { HideCommand,VisibleCommand } from "../actions";
import ChargeAlert from "./ChargeAlert";
import { Link } from 'react-router-dom';

const Charge=(props)=>{
    let them=window.localStorage.getItem('theme')

    const name=useSelector(state=>state.Device.name);
    const operator=useSelector(state=>state.Device.operator);
    const password=useSelector(state=>state.Device.password);
    const number=useSelector(state=>state.Device.number);

    const inquiryCode=(operator==' ' ? '*140*11%23':(operator=='' ? '*141*1%23':(operator=='' ? '*140%23':'')));
    const ChargeCode=(operator==' ' ? '*140*%23':(operator=='' ? '*141*':(operator=='' ? '*140*':'')));
    const inquiryLink='sms://'+number+'?&body=*'+password+'%23charge:'+inquiryCode;
    let chargeLink='sms://'+number+'?&body=*'+password+'%23charge:'+ChargeCode;


    const dispatch=useDispatch();
    
    const  [Alert,setAlert]=useState(false);
    const close_alert=()=>{
        setAlert(false)
    }

    

    const go_to_setting=()=>{
        props.changePage('setting');
        window.history.back()
    }


    return (
        <div className="Charge"  ><div  className='bg-Setting' ><div  className='bgb-Setting' id={them=='white' ? 'light_bg':''}>
            {Alert ? <ChargeAlert close_alert={close_alert}></ChargeAlert> :''}
            {/* <div className="blure"></div> */}
            <div className="header-Setting">
                <div className="backPart-Setting" onClick={go_to_setting}>
                   
                    

                </div>
                <div className="titlPart-Setting" >
                    <p style={{fontWeight:'400',fontSize:'medium'}}>{name}</p>

                </div>
                <div className="refresh">
                    {/* <div className="icon-refresh"></div> */}
                    
                    {/* <div className="text-refresh" > */}
                        {/* <p style={{fontWeight:'500',fontSize:'large'}}> </p> */}

                    {/* </div> */}
                </div>

            </div>
            
            <div class="part1-Charge" id={them=='white' ? 'light_bg':''}>

                <p className="tell-Charge">      ,             </p>
                <div className="increase-Charge">
                    <div className="submitPart-Charge">
                        <button onClick={()=>{setAlert(true)}} id={them=='white' ? "lightButton_bg":""}> </button>
                       
                    </div>
                    <div className="submitPart-Charge">
                    <a href={inquiryLink}><button id={them=='white' ? "lightButton_bg":""}></button></a>
                    

                    </div>
                </div>
            </div>
            </div></div></div>

        
    )
}

export default Charge;