import React, { useState } from "react";
import './ZoonAlert.css';
import {useSelector,useDispatch} from 'react-redux';
import {HideCommand, VisibleCommand} from '../actions/index';
const ZoonAlert=(props)=>{
    const dispatch=useDispatch();
    const number=useSelector(state=>state.Device.number);
    const password=useSelector(state=>state.Device.password);
    const [name,setName]=useState('')

    const name_zoon=(event)=>{
        setName(event.target.value);

    }
    dispatch(HideCommand());
    return(
        <div className="ZoonAlert">
            <div className="alert-ZoonAlert">
                <div className="part-ZoonAlert" id="part1-ZoonAlert">
                    <div className="title-ZoonAlert">
                        <p>  </p>
                    </div>
                    



                </div>
                <div className="part-ZoonAlert" id="part2-ZoonAlert">

                    <div className="inputPart-ZoonAlert">
                        <input type="numberic" onChange={name_zoon} />
                    </div>

                </div>
                <div className="part-ZoonAlert" id="part3-ZoonAlert">
                    <div className="controlers-ZoonAlert">
                        <a href={'sms:'+number+'?&body=*'+password+'%23'+'ZN:'+props.index+':'+name}><div className="send-ZoonAlert"  onClick={props.close_alert}>
                            <p></p>


                        </div></a>
                        <div className="cancle-ZoonAlert" onClick={props.close_alert}>
                            <p></p>
                            
                        </div>
                    </div>

                </div>
                </div>
            

        </div>
    )

}

export default ZoonAlert;