import React, { useState } from 'react';
import './Page1.css';
import Device from  './Device';
// import { Zoom } from 'react-slideshow-image';
// import Carousel from 'react-multi-carousel';
// import {SlideShow} from 'slide-show-component';
// import Carousel from 'react-multi-carousel';
import 'react-multi-carousel/lib/styles.css';
// import Swiper,{Navigation, Pagination} from 'swiper';
import 'swiper/css';
import 'swiper/css/navigation';
import 'swiper/css/pagination';
import 'swiper/css';
import AddDevice from'./AddDevice';
import Cache from 'js-local-cache';
// import Slider, { Range } from 'rc-slider';
import 'rc-slider/assets/index.css';
import { Navigation, Pagination, Scrollbar, A11y } from 'swiper';

import { Swiper, SwiperSlide } from 'swiper/react';

// Import Swiper styles
import 'swiper/css';
import 'swiper/css/navigation';
import 'swiper/css/pagination';
import 'swiper/css/scrollbar';

import 'swiper/css';
import 'swiper/css/navigation';
import 'swiper/css/pagination';
import 'swiper/css/scrollbar';
import { BrowserRouter, Routes, Route, Link } from "react-router-dom";
import { parseMessage } from '../actions';
import { useDispatch, useSelector } from 'react-redux';
import Command from './Command';
import Alert from './Alert';
import {SetDevice,discriptionAlert} from '../actions';




const Page1=(props)=>{
    console.log('app.js:start app');

    const dispatch=useDispatch()
    
    const [isScroll,setIsScroll]=useState('hidden');
    console.log(isScroll)
    const[addDevice,setAddDevice]=useState(false);
    const name=useSelector(state=>state.Device.name);
    // const model=useSelector(state=>state.Device.model);
    const resAlert=useSelector(state=>state.Alert.name);
    const [deleteAlert,setDeleteAlert]=useState(true);
    let them=window.localStorage.getItem('theme');
    
    const add_device=()=>{
        setAddDevice(true);
        Cache.setData('Device','salam');
        

    }
    const close_add=()=>{
        setAddDevice(false)
    }

    const change_state_alert=()=>{
        setDeleteAlert(!deleteAlert)
    }

    
    

      
    let devices=window.localStorage.getItem('devices')!=null ? JSON.parse(window.localStorage.getItem('devices')):[];
  
   const no_scroll=(p1)=>{
    setIsScroll('visible');
    

   }
   const [refresh,setRefresh]=useState(false);
   const refresh_page=()=>{
    setRefresh(!refresh);
    }
    devices.forEach((item,index,arr) => {
        // alert(item.model)
            /*device*/
    const battery=item.battery;
    const emtyBattery='https://s24.picofile.com/file/8452730792/low_battery.png';//10
    const Battery1='https://s24.picofile.com/file/8452289734/battery_6_.png';//11-10
    const lowBattery='https://s24.picofile.com/file/8452731442/batteru_2index.png';//12-11
    const middleBattery='https://s25.picofile.com/file/8452289750/battery_7_.png';//13-12
    const fullBattery='https://s24.picofile.com/file/8452289768/battery_8_.png';//13
    const noBattery='https://s25.picofile.com/file/8452730818/no_battery.png';//nobattery
    let bgBattery='url('+(battery<10 ? emtyBattery:( battery<11 ? Battery1:(battery<12 ? lowBattery:(battery<13 ? middleBattery:(battery>=13 ? fullBattery:emtyBattery)))))+')'
    
    let noActivebg=(them=='white' ?'https://s25.picofile.com/file/8452901176/unlock_5_.png':'https://s25.picofile.com/file/8452659192/unlock_1_.png');
    let activebg=(them=='white'?'https://s25.picofile.com/file/8452901176/unlock_5_.png':'https://s24.picofile.com/file/8452672768/lock_1_.png');
    let disActiveBg=(them=='white'?'https://s25.picofile.com/file/8452901176/unlock_5_.png':'https://s25.picofile.com/file/8452672784/lock_2_.png');
    let stateActiveBg='url('+(item.states.work=="Armed Full" ? activebg:(item.states.work=="Armed Part" ? disActiveBg:noActivebg))+')'
    
    let active=(item.model!= 'AlO21 PT' ?('sms:'+item.number+'?&body=*'+item.password+'%23'+' full'):'');
    let noActive=(item.model!= 'AlO21 PT' ?('sms:'+item.number+'?&body=*'+item.password+'%23'+'disarm'):'');
    let noconnect=(item.model!= 'AlO21 PT' ?('sms:'+item.number+'?&body=*'+item.password+'%23'+'stop'):'');
    let halfActive=(item.model!= 'AlO21 PT' ?('sms:'+item.number+'?&body=*'+item.password+'%23'+'part'):'');
    let refreshPage=(item.model!= 'AlO21 PT' ?('sms:'+item.number+'?&body=*'+item.password+'%23'+'status?'):'');

   const click_setting_device=()=>{
    dispatch(SetDevice(item.name,item.number,item.model,item.password,item.operator))

   }

        arr[index]=(
        <SwiperSlide>
            <div>      
                <div className="Page1  swiper-slide" >
                    <Link to={'/command'}><div className="icon-Command" id={them=='white' ? "lightButton_bg":""}  ></div></Link>
                    {addDevice ? <AddDevice close_add_device={close_add}></AddDevice>:''}

                    <div  className='bg-Page1 '><div  className='bgb-Page1 '  id={them=='white' ? 'light_bg':''} ><div className="Device " id={them=='white' ? 'light_bg':''}> 
    
                        <div className="part1-Device" >
                            <Link to={'Setting'}><div className={them=='white'? "settingL-Device":"setting-Device"}  onClick={click_setting_device}></div></Link>
                            <Link to={'Alert'}><div className={them=='white'? "deleteL-Device":"delete-Device"} ></div></Link>
                            <div className="deviceName-Device">
                                <p>{item.name}</p>
                            </div>
                        </div>
                        <div className="part2-Device">
                            <div className="controller-Device">
                                <a href={refreshPage} ><div className={them=='white'?"logoAL-Device":"logoA-Device"} id={them=='white' ? 'lightButtonnb_bg':''}></div></a>
                                <div className="function">
                                    <a href={active} ><span className='controllerBtns-Device'   id={them=='white' ? 'lightButtonnb_bg':'controllerBtn4-Device'}><div className="partition" id='partition1'><div className="icon-Device" id={them=='white'? 'icon4L-Device':'icon4-Device'}></div><div className="text-Device" ><p id={them=='white' ? 'light_text':''}></p></div></div></span></a>
                                    <a href={noActive} ><span className='controllerBtns-Device'  id={them=='white' ? 'lightButtonnb_bg':'controllerBtn2-Device'}><div className="partition" id='partition2'><div className="icon-Device" id={ them=='white' ? 'icon2L-Device':'icon2-Device'}></div><div className="text-Device"><p id={them=='white' ? 'light_text':''}> </p></div></div></span></a>
                                    <a href={noconnect} ><span className='controllerBtns-Device'  id={them=='white' ? 'lightButtonnb_bg':'controllerBtn1-Device'}><div className="partition" id='partition3'><div className="icon-Device" id={them=='white' ? 'icon1L-Device':'icon1-Device'}></div><div className="text-Device"><p id={them=='white' ? 'light_text':''}>  </p></div></div></span></a>
                                    <a href={halfActive} ><span className='controllerBtns-Device'  id={them=='white' ? 'lightButtonnb_bg':'controllerBtn3-Device'}><div className="partition" id='partition4'><div className="icon-Device" id={them=='white'? 'icon3L-Device':'icon3-Device'}></div><div className="text-Device"><p id={them=='white' ? 'light_text':''}> </p></div></div></span></a>
                                </div>
                            </div>
                        </div>
                    </div>



<div className="part2-Page1" >

{item.model=='AIO21'?(<div className="battery-Page1"  id={them=='white' ? 'light_bg':''}>
    <span className='icons-Page1'><div className='icon-Page1' id='icon1-Page' style={{backgroundImage:(item.states.battery!='No Batt' ? bgBattery:('url('+noBattery+')'))}}></div></span>
    <span className='text-Page1'>
        <div className="title-Page1">
            <p>{(item.states.battery!='No Batt' ? ' ':" ")}</p>
        </div>
        <div className="description-Page1">
            <p>{item.states.battery=='No Batt' ? '  ':item.states.battery}</p>
        </div>
    </span>
</div>):''}

<div className="active-Page1" id={them=='white' ? 'light_bg':''}>
<span className='icons-Page1'><div className='icon-Page1'  id='icon2-Page' style={{backgroundImage:stateActiveBg}}></div></span>
    <span className='text-Page1'>
        <div className="title-Page1">
            <p>{item.states.work=="Armed Full" ? '':(item.states.work=="Armed Part" ? ' ':' ')}</p>
        </div>
        <div className="description-Page1">
            <p> </p>
        </div>
    </span>

</div>
{item.model!='ECO20' ? (<div className="ConnectTel-Page1" id={them=='white' ? 'light_bg':''}>
<span className='icons-Page1'><div className='icon-Page1'   id={them=='white' ? 'icon3L-Page1':'icon3-Page'}></div></span>
    <span className='text-Page1'>
        <div className="title-Page1">
            <p>{item.states.phone=="OFF" ? "":""}</p>
        </div>
        <div className="description-Page1">
            <p>  </p>
        </div>
    </span>

</div>) :''}
<div className="alarm-Page1" id={them=='white' ? 'light_bg':''}>
<span className='icons-Page1'><div className='icon-Page1'  id={them=='white' ? 'icon4L-Page':'icon4-Page'}></div></span>
    <span className='text-Page1'>
        <div className="title-Page1">
            <p>{item.states.ajir=='No Alarm' ? ' ':(item.states.ajir=='Alarming' ? '  ':" ")}</p>
        </div>
        <div className="description-Page1">
            <p> </p>
        </div>
    </span>

</div>
<div className="notif-Page1" id={them=='white' ? 'light_bg':''}>
<span className='icons-Page1'><div className='icon-Page1'  id={them=='white' ? 'icon5L-Page':'icon5-Page'}></div></span>
    <span className='text-Page1'>
        <div className="title-Page1">
            <p>{item.states.beeper=="OFF" ? "": ""}</p>
        </div>
        <div className="description-Page1">
            <p> </p>
        </div>
    </span>

</div>
<div className="connect-Page1" id={them=='white' ? 'light_bg':''}>
<span className='icons-Page1'><div className='icon-Page1'  id={them=='white' ? 'icon6L-Page':'icon6-Page'}></div></span>
    <span className='text-Page1'>
        <div className="title-Page1">
            <p>{item.states.connection=="OFF" ? "":""}</p>
        </div>
        <div className="description-Page1">
            <p>  </p>
        </div>
    </span>
    
</div>



</div>
<div className="part3-Page1">

</div>
</div></div>



</div>



</div>
        </SwiperSlide>
        );
        
    });
      
    return(

        <div  className='bg-Page1'><div  className='bgb-Page1' id={them=='white' ? 'light_bg':''}>
            <div style={{width:'100vw',height:'100vh', position:'fixed',zIndex:'2',visibility:isScroll}}></div>
             {/* <Command name={name} model={model} refresh_page={refresh_page} can_refresh={true} ></Command> */}
            {addDevice ? <AddDevice close_add_device={close_add}></AddDevice>:''}
        {   
        deleteAlert ?(
            <Swiper
            modules={[Navigation, Pagination , Pagination, A11y]}
            spaceBetween={30}
            slidesPerView={1}
            allowTouchMove={isScroll}
            navigation
            pagination={{ clickable: false }}
            scrollbar={{ draggable: false}}
            onSwiper={(swiper) => console.log(swiper)}
            onSlideChange={() => console.log('slide change')}
            
          >
                      
                     {/* {
                        
                     devices.map((item) => {
                        
                        return ({item})
                    })
                    } */}
                    {devices}
                   
                    
                    <SwiperSlide>
                        <div className="Page1  swiper-slide" style={{height:'100vh'}} >
                            <div className="addDevice-Page1 " >
                            <div className="contentAdd-Page1" id={them=='white' ? 'light_bg':''}>
                                    <div className="centerAdd-Page1"  onClick={add_device}>
 
                                        <div  className='img-Device' id={them=='white' ? 'border_light':''}>
                                            <div className="borer-img" id='borer-img' >
                                                
                                            </div>

                                        </div>

                                        <div className="textAdd-Page1">
                                        
                                        <p style={{fontSize:'medium',fontWeight:'400'}}>  </p>

                                    </div>

                                    </div>


                                </div>
                            </div>
                            <div className="part2Add-Page1">
                                <Link to={'/settingG'}><div className="settingAdd-Pag1" id={them=='white' ? 'light_bg':''}>
                                    <div className={them=='white'?'iconASL-Page1':"iconAS-Page1"} >

                                    </div>
                                    <span className='textAS-Page1'>   </span >
                                </div></Link>
                                <div className="aboutAdd-Pag1" id={them=='white' ? 'light_bg':''}>
                                    <div className={them=='white'?'iconAAL-Page1':"iconAA-Page1"}>
                                        
                                    </div>
                                    <p className='textAA-Page1'> </p>
                                </div>
                                <div className="helpAdd-Pag1" id={them=='white' ? 'light_bg':''}>
                                    <div className={them=='white'?"iconAHL-Page1":"iconAH-Page1"} >
                                        
                                    </div>
                                    <p className='textAH-Page1'></p>
                                </div>
                            </div>
                        </div>
                    </SwiperSlide>

        
        

      </Swiper>
        ):(<Alert close_alert={change_state_alert}></Alert>)
      } 
</div></div>

    )
}
export default Page1;