import React, { useState } from 'react';
import { useSelector ,useDispatch} from 'react-redux';
import './SmsPart.css';
import { Update } from '../actions';
import { useEffect } from 'react';


const SmsPart=(props)=>{
    console.log('smsPart: data is');
    let them=window.localStorage.getItem('theme')
    
    
    const number=useSelector(state=>state.Device.number);
    const password=useSelector(state=>state.Device.password);
    // const receiveText=props.text;
    const [text,setText]=useState('')
    const [refresh,setRefresh]=useState(false);
    let send='sms:'+number+'?&body=*'+password+'%23text:'+props.index+':'+text;
    let receive='sms:'+number+'?&body=*'+password+'%23text?:'+props.index;
    const shortHeight='62px';
    const LongHeight='250px';
    const dispatch=useDispatch()
    
    const [height,setHeight]=useState('0');
    const [rotate,setRotate]=useState('rotate(90deg)');
    const text_sms=(event)=>{
        setText(event.target.value);

    }

    
    const open_sms_part=()=>{
        
        
        if(height=='1' && props.index==props.smsSelector){
            props.setSmsSelecor(props.index);
            setHeight('0');
        }else{
            props.setSmsSelecor(props.index);
            setHeight('1');
        }

        
        

    }
    const set_info_update=()=>{
        
        dispatch(Update(props.index))
    }

    return(
        <div className="SmsPart" style={{height:((props.index==props.smsSelector && height=='1') ? LongHeight:'62px')}} id={them=='white' ? 'light_bg':''}> 
            
            <div id="part1-SmsPart">
                <div className={them=='white' ? "iconL-SmsPart":'icon-SmsPart'}>

                </div>
                <div className="description-SmsPart">
                    <p>{props.name}</p>
                    {/* <p >{props.text}</p> */}
                </div>
                <div className={them=='white'?'openL-SmartPart':"open-SmartPart"} onClick={open_sms_part} id={them=='white' ? 'lightButton_bg':''}>

                </div>

            </div>

            <div id="part2-SmsPart">
                <div className="input-SmsPart"  >
                    
                    
                    <textarea type="text" onChange={text_sms} style={{fontSize:'small',fontWeight:'400'}} maxLength={60} defaultValue={props.text!='' ? props.text:''} placeholder={props.text=='' ? '':''} id={them=='white' ? 'lightButtonnb_bg':''}></textarea>
      
                </div>
                <div className="controllers-SmsPart">
                    <a href={send}><button id={them=='white' ? 'lightButton_bg':''}></button></a>
                    <a href={receive} onClick={set_info_update}><button id={them=='white' ? 'lightButton_bg':''}></button></a>

                </div>

            </div>
            
            
            

        </div>
    )
}

export default SmsPart;